import { createApp } from "vue";
import App from "./App.vue";
import "./main.css";
import * as VueRouter from "vue-router";
import ShoppingCartPage from "./pages/ShoppingCartPage.vue";
import ProductsPage from "./pages/ProductsPage.vue";
import ProductDetailPage from "./pages/ProductDetailPage.vue";
import NotFoundPage from "./pages/NotFoundPage.vue";
import { initializeApp } from "firebase/app";

const firebaseConfig = {
  apiKey: "AIzaSyCYRrcdtHRDPtVvhS-omuG8BrbthJeVxe0",
  authDomain: "full-stack-vue-site.firebaseapp.com",
  projectId: "full-stack-vue-site",
  storageBucket: "full-stack-vue-site.firebasestorage.app",
  messagingSenderId: "877170569866",
  appId: "1:877170569866:web:7264d8cd4b8ff500963373"
};

initializeApp(firebaseConfig);

createApp(App)
  .use(
    VueRouter.createRouter({
      history: VueRouter.createWebHistory(process.env.BASE_URL),
      routes: [
        { path: "/cart", component: ShoppingCartPage },
        { path: "/products", component: ProductsPage },
        { path: "/products/:productId", component: ProductDetailPage },
        { path: "/:pathMatch(.*)*", component: NotFoundPage },
      ],
    })
  )
  .mount("#app");
